* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

body {
  background-color: rgb(43, 43, 43);
}

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
  background-color: rgb(43, 43, 43);
}

.header {
  margin-top: 4%;
  text-align: center;
  margin-bottom: 30px;
}

.header h1 {
  color: rgb(255, 255, 255);
  font-size: 1.8em;
} 

.header h3 {
  color: rgb(194, 166, 6);
  font-size: 1.1em;
} 

.foto {
  width: 150px;
  border-radius: 100%;
  box-shadow: 0 0 1em gold;
  margin-bottom: 10px;
}

.dev {
  margin-top: 5px;
  color: white !important;
  font-size: 1.3em !important;
}

.content {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 600px;
  flex-wrap: wrap;
  padding: 10px;
}

.square1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  width: 280px;
  height: 150px;
  background-color: rgb(61, 60, 60);
  transform: scale(0.9);
  transition: all 0.5s ease;
}

.square2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  width: 280px;
  height: 150px;
  background-color: rgb(61, 60, 60);
  transform: scale(0.9);
  transition: all 0.5s ease;

}

.square3 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  width: 280px;
  height: 150px;
  background-color: rgb(61, 60, 60);
  transform: scale(0.9);
  transition: all 0.5s ease;
}

.square4 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  width: 280px;
  height: 150px;
  background-color: rgb(61, 60, 60);
  transform: scale(0.9);
  transition: all 0.5s ease ;
}

.icon {
  font-size: 40px;
  margin-bottom: 5px;
}

.square1:hover,
.square2:hover,
.square3:hover,
.square4:hover {
  transform: scale(1);
  box-shadow: 0 0 0.2em gold;
  color: gold;
  font-weight: 900;
  font-size: 1.5em;
  cursor: pointer;
}

.port-container {
  display: flex;
  flex-direction: column;
  align-content: center;
}

.port {
width: 400px;
display: flex;
justify-content: center;
align-items: center;
}

.port img {  
  width: 800px;
  transform: scale(0.9);
  transition: all 0.5s ease ;
  }

.port img:hover {  
  transform: scale(1);
  box-shadow: 0 0 0.4em gold;
  color: gold;
  font-weight: 900;
  cursor: pointer;
  }

.port-btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  padding: 10px;
  font-size: 1em;
  background-color: rgb(61, 60, 60);
  transform: scale(0.9);
  transition: all 0.5s ease ;
  margin-bottom: 15px;
}
.port-btn:hover {
  transform: scale(1);
  box-shadow: 0 0 0.2em gold;
  color: gold;
  font-weight: 900;
  cursor: pointer;
}

@media only screen and (max-width: 760px),
(min-device-width: 468px) and (max-device-width: 890px)  {
  .container {
    height: 150vh;
  }
}

@media only screen and (max-device-width: 590px)  {
  .header {
    font-size: 1.8em;
  }

  .foto {
    width: 300px;
  }
  
  .content {
    width: 100%;
    align-items: center;
    justify-content: center;
  }
  .square1,
  .square2,
  .square3,
  .square4 {
   width: 400px;
   height: 200px;
   font-size: 1.8em;
  }
  .icon {
    font-size: 1.8em;
  }
}